import React from "react";
import PropTypes from "prop-types";
import SuperGeilesMenu from "../Article/SuperGeilesMenu";

import Headline from "../Article/Headline";
import Bodytext from "../Article/Bodytext";

const Page = props => {
  const {
    page: {
      html,
      frontmatter: { title }
    },
    theme
  } = props;

  return (
    <React.Fragment>
      <header>
        <Headline title={title} theme={theme} />
      </header>
      <div className="wrapper">
      <div className="main">
         <Bodytext html={html} theme={theme} />
      </div>
      <div className="sidebar">
        <ul>
        {createLinks()}
        </ul>
      </div>
    </div>
     {/* --- STYLES --- */}
      <style jsx>{`

@media only screen and (max-width: 1026px) {
    .sidebar {
      visibility: hidden;
    }
}
.wrapper {
  display: flex;
  justify-content: space-between;
  width: 800px;
}

.sidebar ul{
  list-style:none;
  position:relative;
  overflow: hidden;
}

.lititleinner{
  font-weight: bold;
  font-size: smaller;

}

.sidebar {
  background: #e8e8e8 !important;
  padding: 15px;
  background-color: #fff;
  position: -webkit-sticky;
  position: sticky;
  top: 10vh;
  font-size: smaller;
    display: table;

}

.main {
  width: 78%;
}

.sidebar {
  width: 20%;
}
`}</style>
    </React.Fragment>

  );
};

Page.propTypes = {
  page: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired
};

export default Page;
